
.movies .movie{
    width:45%;
    background-color: rgb(254, 253, 255);
    margin-bottom:70px;/*외부로 띄어지는 것*/
    display:flex;
    align-items:flex-start;
    justify-content: space-between;
    font-weight: 300;/*글자 굵기*/
    padding:20px;/*흰창안에 정보들을 흰창과 떨어뜨림 */
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25),
    0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}

.movie img{
    position: relative;/*원위치 기준으로 아래 top등을 사용해서 가운데로 모아줌.*/
    top:-50px;/*가운데가 아니라 나가고 싶으므로 -를 붙여준다*/
    max-width:180px;
    margin-right:30px; /*사진 오른쪽만 더 띄어주기*/
    box-shadow: 0 30px 60px -12px rgba(50,50,93,0.25),
    0 18px 36px -18px rgba(0,0,0,0.3), 0 -12px 36px -8px rgba(0,0,0,0.025);
}

.movie .movie__title,
.movie .movie__year{
    margin:0;
    font-weight:300;
}

.movie .movie__title{
    margin-bottom:5px;
    font-size:24px;
    color:#2c2c2c;
}

.movie .movie__genres{
    list-style: none;
    padding:0;
    margin:0;
    display: flex;
}

.movie__genres li{
    margin-right: 10px;
    font-size:14px;
}